body {
  /* margin: 20; */
  padding: 20;
  font-family: Roboto, sans-serif;
  font-weight: 200;
  height: 100%;
  margin:0;
}


h1 {
  font-weight: 300;
  font-size: 2rem;
  color: #3f51b5;
}
h2 {
  font-weight: 300;
  font-size: 1.2rem;
}

h4 {
  font-weight: 400;
}

strong{
  font-weight: 500;
}

html {
  background: #ECE9E6;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #ECE9E6, #FFFFFF);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(rgb(236, 233, 230)), to(rgb(255, 255, 255)));
  background: -webkit-linear-gradient(right, rgb(236, 233, 230), rgb(255, 255, 255));
  background: -o-linear-gradient(right, rgb(236, 233, 230), rgb(255, 255, 255));
  background: linear-gradient(to left, rgb(236, 233, 230), rgb(255, 255, 255));
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* height: 600px;
  width: 800px; */
  height: 100%;
}


/* REACT-SPINNER */

.react-spinner {
  position: relative;
  width: 32px;
  height: 32px;
  top: 50%;
  left: 50%;
}

.react-spinner_bar {
  -webkit-animation: react-spinner_spin 1.2s linear infinite;
  animation: react-spinner_spin 1.2s linear infinite;
  border-radius: 5px;
  background-color: rgb(0, 0, 0);
  position: absolute;
  width: 20%;
  height: 7.8%;
  top: -3.9%;
  left: -10%;
}

@keyframes react-spinner_spin {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.15;
  }
}

@-webkit-keyframes react-spinner_spin {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.15;
  }
}

#root{
  border: 4px solid red;
  max-height: 100%;
  overflow: scroll;
}

#mainDrawerContent{
  max-height: 100%;
  max-height: -moz-available;          /* WebKit-based browsers will ignore this. */
  max-height: -webkit-fill-available;  /* Mozilla-based browsers will ignore this. */
  max-height: fill-available;
}


